<template>
  <v-container>
    <Breadcrumbs 
      title="Reports (User)"
      icon="mdi-clipboard-alert"
      :items="breadcrumbsItems"
    />
    <DashboardTitle />

    <v-card>
      <validation-observer
        ref="observer"
        v-slot="{ invalid }"
      >
        <v-card-title>
          Add daily reports
        </v-card-title>

        <v-card-text>
          <v-alert
            v-model="alert.isOpen"
            border="left"
            :type="alert.type"
            :color="alert.color"
            dismissible
          >
            {{alert.message}}
          </v-alert>

          <validation-provider
            name="Amount of vials used"
            v-slot="{ errors }"
            rules="required|numeric|min_value:1"
          >
            <v-text-field
              label="Amount of vials used"
              v-model="form.amount"
              :disabled="isSaving"
              :error-messages="errors"
            />
          </validation-provider>
        </v-card-text>

        <v-card-actions>
          <v-spacer />

          <v-btn
            color="success"
            :disabled="invalid || isSaving"
            :loading="isSaving"
            @click="save"
          >
            Save
          </v-btn>
        </v-card-actions>
      </validation-observer>
    </v-card>
  </v-container>
</template>

<script>
import {ValidationProvider, ValidationObserver} from 'vee-validate'
import Breadcrumbs from '../../components/admin/Breadcrumbs.vue'
import DashboardTitle from '../../components/admin/DashboardTitle.vue'
import axios from 'axios'

export default {
  name: 'UserReports',
  components: { 
    DashboardTitle,
    Breadcrumbs,
    ValidationProvider,
    ValidationObserver,
  },
  data: () => ({
    breadcrumbsItems: [
      {
        text: 'Home',
        disabled: false,
      },
      {
        text: 'Reports',
        disabled: false,
      },
    ],
    alert: {
      isOpen: false,
      type: 'success',
      message: '',
      color: 'success',
    },
    form: {
      amount: ''
    },
    isSaving: false,
  }),

  methods: {
    async save () {
      this.isSaving = true
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        await axios.post(
          `${apiHost}/daily-reports`,
          this.form,
          config
        )

        this.successAlert()
        this.form.amount = ''
        this.$refs.observer.reset()

        this.$emit('created')
      } catch (error) {
        console.log('error')
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/user/login').catch()
        } else if (error?.response?.status === 400) {
          this.alert.type = 'error'
          this.alert.color = 'error'
          this.alert.message = error.response.data.message
          this.alert.isOpen = true
        } else {
          this.somethingWentWrongAlert()
        }
      }

      this.isSaving = false
    },

    somethingWentWrongAlert() {
      this.alert.type = 'warning'
      this.alert.color = 'warning'
      this.alert.message = 'Something went wrong. Please contact your IT admin.'
      this.alert.isOpen = true
    },

    successAlert() {
      this.alert.type = 'success'
      this.alert.color = 'success'
      this.alert.message = 'Form successfully submitted'
      this.alert.isOpen = true
    },
  }
}
</script>
